import 'assets/css/frr.css';
import 'assets/css/qrCertificationHistory.css';
import 'react-datepicker/dist/react-datepicker.css';
import { Grid } from 'gridjs-react';
import 'gridjs/dist/theme/mermaid.min.css';
import { data, gridColumn } from 'app/data/deviceManagement/stations/gridData';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';

function Faq() {
  const navigate = useNavigate();
  const groupList = ['그룹1', '그룹2'];
  const [group, setGroup] = useState('');

  const gridRowClick = (e) => {
    const rowObj = {};
    if (e.target.getAttribute('data-column-id') !== null) {
      e.target.parentNode.childNodes.forEach((obj) => {
        rowObj[`${obj.getAttribute('data-column-id')}`] = obj.textContent;
      });
      console.log(rowObj);
      rowObj.flag = '수정';
      navigate('/helpdesk/notice/addOrUpdate', {
        state: rowObj,
      });
    }
  };

  return (
    <div className="section__grid__wrap content qr__history frr">
      <section className="qr__history__search">
        <div className="section__inner">
          <div className="input__top__wrap">
            <select className="custom__select" onChange={(e) => setGroup(e.target.value)}>
              <option value="">프로젝트</option>
            </select>
            <ul className="checkbox__wrap">
              <li>
                <dl>
                  <dt>
                    <label htmlFor="checkbox1">공개</label>
                  </dt>
                  <dd>
                    <input className="custom__checkbox" type="checkbox" id="checkbox1" />
                  </dd>
                </dl>
              </li>
              <li>
                <dl>
                  <dt>
                    <label htmlFor="checkbox2">비공개</label>
                  </dt>
                  <dd>
                    <input className="custom__checkbox" type="checkbox" id="checkbox2" />
                  </dd>
                </dl>
              </li>
            </ul>
          </div>
          <div className="search__wrap groups">
            <div className="left">
              <div className="input__wrap">
                <dl>
                  <dt>장치명</dt>
                  <dd>
                    <input
                      type="text"
                      className="custom__input"
                      placeholder="장치명을 입력하세요"
                    />
                  </dd>
                </dl>
              </div>
              <button className="custom__btn">
                <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                  />
                </svg>
                <span className="f__medium">검색</span>
              </button>
            </div>
            <button
              className="custom__btn add__btn"
              onClick={() => {
                navigate('/helpdesk/notice/addOrUpdate', {
                  state: { flag: '추가' },
                });
              }}
            >
              <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 6v6m0 0v6m0-6h6m-6 0H6"
                />
              </svg>
              <span className="f__medium">추가</span>
            </button>
          </div>
          <div
            className="grid__wrap"
            onClick={(e) => {
              gridRowClick(e);
            }}
          >
            <Grid
              sort="true"
              className="grid"
              data={data}
              columns={gridColumn}
              pagination={{
                enabled: true,
                limit: 10,
              }}
            />
          </div>
        </div>
      </section>
    </div>
  );
}

export default Faq;
